.App {
  text-align: center;
  background-color: var(--tg-theme-bg-color, #ffffff);
  color: var(--tg-theme-text-color, #000000);
  min-height: 100vh;
  padding: 16px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  padding: 20px;
  margin-bottom: 20px;
}

.App-header h1 {
  font-size: 24px;
  margin: 0;
  margin-bottom: 10px;
  color: var(--tg-theme-text-color, #000000);
}

.App-header p {
  font-size: 16px;
  margin: 0;
  color: var(--tg-theme-hint-color, #999999);
}

.App-main {
  padding: 0 16px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
